:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-light: hsl(230, 22%, 74%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle-light: hsl(230, 19%, 60%);
  --toggle-button-light: hsl(228, 46%, 96%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-card-hover: hsl(228, 33%, 91%);
  --light-text1: hsl(230, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
  --background: var(--light-bg);
  --text-color1: var(--light-text1);
  --text-color2: var(--light-text2);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--dark-bg);
    --text-color1: var(--dark-text1);
    --text-color2: var(--dark-text2);
    --card-bg: var(--dark-card);
    --card-hover: var(--dark-card-hover);
    --toggle: var(--light-bg);
    --toggle-bg: linear-gradient(
      225deg,
      var(--toggle-bg-end) 0%,
      var(--toggle-bg-start) 98.02%
    );
    --toggle-button: var(--dark-bg);
  }
}
body.light {
  --background: var(--light-bg);
  --text-color: var(--light-text2);
  --text-color2: var(--light-text1);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
}

body.dark {
  --background: var(--dark-bg);
  --text-color1: var(--dark-text1);
  --text-color2: var(--dark-text2);
  --card-bg: var(--dark-card);
  --card-hover: var(--dark-card-hover);
  --toggle: var(--light-bg);
  --toggle-bg: linear-gradient(
    225deg,
    var(--toggle-bg-end) 0%,
    var(--toggle-bg-start) 98.02%
  );
  --toggle-button: var(--dark-bg);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--background);
  color: var(--text-color);
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
  color: var(--text-color2);
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
  color: var(--text-color2);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}

.header {
  margin-top: 1.5625rem;
}
@media (min-width: 40em) {
  .header {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}
.header__title {
  margin-bottom: 1.25rem;
}
@media (min-width: 40em) {
  .header__title {
    margin-bottom: 0;
  }
}
.header__title::after {
  content: "";
  display: block;
  height: 1px;
  background-color: var(--light-text1);
  margin-bottom: 20px;
  margin-top: 20px;
}
@media (min-width: 40em) {
  .header__title::after {
    display: none;
  }
}
.header__title--main {
  color: var(--text-color2);
}
.header__title--sub {
  font-size: 0.875rem;
  color: var(--text-color1);
  font-weight: 700;
}
.toggle {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 40em) {
  .toggle {
    justify-content: end;
  }
}
.toggle__label {
  cursor: pointer;
  font-weight: 700;
  color: var(--text-color1);
}
@media (min-width: 40em) {
  .toggle__label {
    margin-right: 3.5rem;
  }
}
.toggle__btn {
  appearance: none;
  position: relative;
  cursor: pointer;
}
.toggle__btn::before {
  content: "";
  position: absolute;
  width: 3rem;
  height: 1.5rem;
  right: 0;
  top: -0.6875rem;
  background: var(--toggle-bg);
  border-radius: 100px;
}
.toggle__btn::after {
  content: "";
  position: absolute;
  top: -0.5625rem;
  right: 0.1875rem;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: var(--toggle-button);
  transition: all 0.3s ease;
}
.toggle__btn:checked::after {
  right: 1.5625rem;
}

.cards {
  font-size: 1rem;
  display: grid;
  gap: 1.25rem;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  position: relative;
  background-color: var(--card-bg);
  padding: 1.5625rem;
  border-radius: 0.5rem;
  overflow: hidden;
}
.card__main::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 0.3125rem;
  background-color: gray;
}
.card--facebook::before {
  background: var(--facebook);
}
.card--instagram::before {
  background: linear-gradient(225deg, var(--instagram-end), var(--instagram-middle) 50.91%, var(--instagram-start) 100%);
}
.card--twitter::before {
  background: var(--twitter);
}
.card--youtube::before {
  background: var(--youtube);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 0.75rem;
  margin-bottom: 1.25rem;
}
.card__icon {
  width: 1.5rem;
  height: 1.5rem;
}
.card__username {
  font-weight: 700;
  color: var(--text-color1);
  font-size: 0.875rem;
}
.card__followers {
  text-align: center;
  margin-bottom: 1.5rem;
}
.card__count {
  font-weight: 700;
  color: var(--text-color2);
}
.card__count--big {
  font-size: 3.5rem;
}
.card__count--small {
  font-size: 2rem;
}
.card__label {
  text-transform: uppercase;
  letter-spacing: 0.1875rem;
  color: var(--text-color1);
}
.card__change {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 0.3125rem;
}
.card__change__icon {
  width: 0.5rem;
}
.card__change__number {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text-color1);
}
.card .card__top {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 1.25rem;
}
.card__stats-type {
  font-weight: 700;
  color: var(--text-color1);
  font-size: 0.875rem;
}
.card .card__bottom {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.card-grid {
  background: var(--background);
}